.nav{
    position:fixed;
    top:0;
    padding:10px;
    width:100%;
    height: 30px;
    z-index: 1;

    /* animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;

}
.nav-content{
    display: flex;
    justify-content:space-between ;
}
.nav-logo{
    position: fixed;
    top: 5px;
    left: 0;
    width: 80px;
    padding-left: 20px;
    cursor: pointer;
    object-fit: contain;
}
.nav-avatar{
    cursor: pointer;
    position:fixed;
    right:20px;
    width: 30px;
    
}
.nav-black{
    background-color: black;
}