.row{
   color:white;
   margin-left:20px;
}
.row-posters{
    display: flex;
    overflow-y:hidden;
    overflow-x: scroll;
    padding:20px;
}
.row-posters::-webkit-scrollbar{
    display: none;
}
.row-poster{
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    transition: transform 450ms;
}
.row-poster:hover{
  
  transform: scale(1.08);
  opacity:1;
}

.row-posterLarge{
    max-height: 150px;
}